import React, { lazy, useEffect, useState, Suspense } from "react";
import {
  Container,
  Typography,
  Grid,
  CircularProgress,
  Button,
  Dialog,
  IconButton,
  DialogTitle,
  DialogContent,
} from "@material-ui/core";
import api from "../api/api";
import baseurl from "../api/baseurl";
import { Close } from "@material-ui/icons";
import { connect } from "react-redux";

const Login = lazy(() => import("../components/Login"));
const Registration = lazy(() => import("../components/Registration"));
const BulkProduct = ({ auth, profile }) => {
  const [openMainDialog, setOpenMainDialog] = useState(false);
  const [values, setValues] = useState({});
  const [product, setProduct] = useState([]);
  const [openSuccessDialog, setOpenSuccessDialog] = useState(false);
  const [openLoginDialog, setOpenLoginDialog] = useState(false);
  const [openRegistrationDialog, setOpenRegistrationDialog] = useState(false);

  useEffect(() => {
    document.title = "Bulk Products";
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    try {
      const response = await api.get(`/api/bulk`);
      return setProduct(response.data.data);
    } catch (err) {
      return;
    }
  };

  const submit = async () => {
    // e.preventDefault();
    try {
      // setWait(true);

      await api.post(`/api/bulk-order`, {
        bulk: values.id,
        customer: profile.id

      });

      // setWait(false);
      // setOpenDialog(false);
      setOpenSuccessDialog(true);
    } catch (err) {
      return alert("Sorry, something went wrong! Please try again.");
    }
  };

  const renderProducts = () => {
    if (product.length)
      return (
        <Grid container style={{ marginTop: 20 }} spacing={3}>
          {product

            .map((a, i) => {
              return (
                <Grid
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setValues(a);
                    setOpenMainDialog(true);
                  }}
                  item
                  key={i}
                  xs={12}
                  sm={12}
                  md={6}
                  lg={3}
                  xl={3}
                >
                  <div
                    style={{
                      border: "1px solid #eee",
                      boxShadow: "1px 1px 8px #ddd",
                      padding: 0,

                    }}
                  >
                    <img
                      alt={a.name}
                      src={`${baseurl}${a.image}`}
                      style={{ objectFit: "contain", height: 220, width: "100%" }}
                    />
                    <div
                      style={{
                        backgroundColor: "#555",
                        position: "sticky",
                        opacity: 0.8,
                        marginTop: -45,
                        padding: 10,
                      }}
                    >
                      <Typography style={{ color: "white", fontWeight: 500 }}>
                        {a.name}
                      </Typography>
                    </div>
                  </div>
                </Grid>
              );
            })}
        </Grid>
      );
  };

  const renderMainDialog = () => {
    if (values) {
      return (
        <Dialog
          scroll="body"
          fullWidth
          maxWidth="xs"
          open={openMainDialog}
          onClose={() => {
            setValues({});
            setOpenMainDialog(false);
          }}
        >
          <div>
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                width: "100%",
                position: "absolute",
              }}
            >
              <IconButton
                onClick={() => {
                  setValues({});
                  setOpenMainDialog(false);
                }}
              >
                <Close />
              </IconButton>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",

              }}
            >
              <img
                alt={values.name}
                src={`${baseurl}${values.image}`}
                style={{ objectFit: "contain", height: 300, width: "100%" }}
              />
            </div>
            <div style={{ padding: 10 }}>
              <Typography variant="h6">{values.name}</Typography>
              <Typography style={{ whiteSpace: "pre-line", marginTop: 10 }}>
                {values.description}
              </Typography>
              <div
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  marginTop: 20,
                }}
              >
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    if (auth) {
                      if (
                        window.confirm(
                          `Are you sure you want to order this product?`
                        )
                      ) {
                        return submit();
                      }
                    } else {
                      setOpenLoginDialog(true);
                    }
                  }}
                >
                  Place Order
                </Button>
              </div>
            </div>
          </div>
        </Dialog>
      );
    }
  };

  return (
    <Container maxWidth="md">
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 20, marginTop: 80 }} color="primary">
        Bulk Products
          </Typography>
      {renderProducts()}
      {renderMainDialog()}


      <Dialog
        fullWidth
        maxWidth="xs"
        open={openSuccessDialog}
        onClose={() => {
          setOpenSuccessDialog(false);
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            padding: 20,
          }}
        >
          <img
            alt="Success"
            src="/success.png"
            style={{
              width: 80,
            }}
          />
          <Typography
            align="center"
            variant="h6"
            style={{
              color: "#009688",
            }}
          >
            Your order has been submitted successfuly
          </Typography>
          <Typography
            align="center"
            variant="h6"
            style={{
              color: "#009688",
            }}
          >
            We shall contact you soon
          </Typography>
          <Button
            onClick={() => {
              setOpenSuccessDialog(false);
            }}
            style={{ marginTop: 20, borderRadius: 50 }}
            variant="contained"
            color="primary"
          >
            OK
          </Button>
        </div>
      </Dialog>
      {/* <Dialog
        fullWidth
        maxWidth="xs"
        open={openDialog}
        onClose={() => {
          setOpenDialog(false);
        }}
      >
        <DialogTitle>Note</DialogTitle>
        <DialogContent dividers>
          <form onSubmit={submit}>
            <TextField
              required
              fullWidth
              variant="outlined"
              multiline
              rows={5}
              margin="dense"
              size="small"
              label="Please type any words to us"
              value={formValues.note}
              onChange={(e) =>
                setFormValues({ ...formValues, note: e.target.value })
              }
            />

            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                marginTop: 10,
              }}
            >
              <Button
                disabled={wait}
                type="submit"
                variant="contained"
                color="primary"
              >
                Confirm
              </Button>
            </div>
          </form>
        </DialogContent>
      </Dialog> */}
      <Dialog
        fullWidth
        maxWidth="xs"
        open={openLoginDialog}
        onClose={() => {
          setOpenLoginDialog(false);
        }}
      >
        <DialogTitle>Login</DialogTitle>
        <DialogContent dividers>
          <Suspense
            fallback={
              <div
                style={{
                  marginTop: 400,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <CircularProgress />
              </div>
            }
          >
            <Login
              setOpenRegistrationDialog={setOpenRegistrationDialog}
              setOpenLoginDialog={setOpenLoginDialog}
            />
          </Suspense>
        </DialogContent>
      </Dialog>
      <Dialog
        fullWidth
        maxWidth="xs"
        open={openRegistrationDialog}
        onClose={() => {
          setOpenRegistrationDialog(false);
        }}
      >
        <DialogTitle>Registration</DialogTitle>
        <DialogContent dividers>
          <Suspense
            fallback={
              <div
                style={{
                  marginTop: 400,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <CircularProgress />
              </div>
            }
          >
            <Registration
              setOpenRegistrationDialog={setOpenRegistrationDialog}
              setOpenLoginDialog={setOpenLoginDialog}
            />
          </Suspense>
        </DialogContent>
      </Dialog>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    profile: state.profile,
  };
};

export default connect(mapStateToProps, null)(BulkProduct);
